strong {
    font-weight: lighter;
    font-size: 1.2rem;
    margin-bottom: 1rem;
    text-transform: uppercase;
}
#about-container h1 {
    font-weight: lighter;
    margin-bottom: 1rem;
    text-align: center;
    margin-top: 1rem;
}
#about-info {
    display: flex;
    flex-direction: row;
    align-items: stretch;
    justify-content: space-between;
    height: 100%;
    margin: 1rem;
}
h1 {
    display: block;
}
ul {
    list-style-position: inside;
}
#about1 {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    flex: 1;
}
.underline {
    display: inline-block;
    text-decoration: underline;
}
em {
    font-weight: bold;
}
.about-title {
    font-size: 2rem;
    margin-bottom: 1rem;
}
#about2 {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    flex: 1;
}
#about2 ul li {
    font-weight: bold;
}

@media (max-width: 900px) {
    #about-info {
        flex-direction: column;
    }
    #about1 {
        margin-bottom: 2rem;
    }
}