#contact-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    height: 100%;
    color: #898989;
    margin: 1rem;
}
#contact-info {
    padding: 1rem;
}
#title {
    font-size: 2.5rem;
    font-weight: bold;
    margin-bottom: 1rem;
}
#map-container{
    overflow:hidden;
    padding-bottom:50.25%;
    position:relative;
    height:0;
    width: 50%;
}

#map-container iframe{
    left:0;
    top:0;
    height:100%;
    width:100%;
    position:absolute;
}
strong {
    font-weight: lighter;
    font-size: 1.2rem;
    margin-bottom: 1rem;
    text-transform: uppercase;
}
#location-container {
    margin-top: 1rem;
    margin-bottom: 1rem;
}
#location-container * {
    margin-bottom: 1rem;
}
#location-container a {
    color: #898989;
}
#hours-container {
    padding-bottom: 1rem;
}
#hours-container li {
    padding: 0.5em 0;
}
#socials-container {
    padding: 0.5em 0;
}

@media (max-width: 749px) {
    #contact-container {
        background-color: #E9E9E6;
        flex-direction: column;
    }
}