#slider-wrapper {
    z-index: -1;
}
.slide-container {
    max-width: 100%;
    margin: auto;
    height: auto;
  }
  
  h3 {
    text-align: center;
  }

  #image {
      background-size: contain;
      /* center the image */
      background-repeat: no-repeat;
      background-position: center;
  }
  
  .each-slide > div {
    display: flex;
    align-items: center;
    justify-content: center;
    background-repeat: no-repeat;
    height: 50vh;
  }
  
  .slide-caption {
    padding: 20px;
    font-size: 20px;
    color: #fff;
    background: rgba(0, 0, 0, 0.7);
    text-align: center;
  }
  #react-slideshow-wrapper {
    pointer-events: none;
  }
  
  .each-fade {
    display: flex;
  }
  
  .each-fade .image-container {
    width: 100%;
    overflow: hidden;
  }
  
  .each-fade .image-container img {
    width: 100%;
  }
  
  .each-fade h2 {
    width: 25%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    background: #adceed;
  }
  
  .indicator {
    width: 30px;
    color: blue;
    text-align: center;
    cursor: pointer;
    border: 1px blue solid;
  }
  .indicator.active {
    background: #000;
    color: #fff;
  }
  