* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

#header {
    display: flex;
    justify-content: space-between;
    background-color: #2E2E2E;
    align-items: center;
    white-space: nowrap;
}

#header .logo img {
    -webkit-user-drag: none;
    -khtml-user-drag: none;
    -moz-user-drag: none;
    -o-user-drag: none;
    user-drag: none;
    width: 90px;
    height: 90px;
    padding: 0;
    margin-left: 10px;
}

#header .logo {
    z-index: 100;
}

#header ul {
    display: flex;
    z-index: 99;
}

#header ul li {
    list-style: none;
    padding: 15px 15px;
}

#header ul li {
    list-style: none;
    position: relative;
    width: 100%;
}

#header ul li:after {
    content: '';
    position: absolute;
    left: 50%;
    bottom: -1px;
    width: 0px;
    height: 2px;
    background: #D67B60;
    transition: all 0.45s;
}

#header ul li:hover:after {
    width: 100%;
    left: 0;
}

#header ul li a:hover {
    text-decoration: none;
}

#header ul li a {
    text-decoration: none;
    font-size: 24px;
    color: #F9F9F9;
    font-weight: 400;
}

#header ul li button {
    font-size: 24px;
    color: #F9F9F9;
    background-color: inherit;
    border: none;
    font-weight: 400;
}

#header ul #list-dropdown:after {
    background: inherit;
}

#header ul li img {
    margin-right: 30px;
}

.toggle {
    display: none;
    -webkit-tap-highlight-color: transparent;
}

@media(max-width:749px) {
    #header ul {
        position: absolute;
        top: -100%;
        left: 0;
        width: 100%;
        height: calc(100vh- 80px);
        padding: 50px 100px;
        text-align: center;
        overflow: auto;
        background-color: #2E2E2E;
        flex-direction: column;
        opacity: 0;
    }

    #header ul li:after {
        background: inherit;
    }

    .logo {
        color: #fff;
    }

    #header {
        background-color: #2E2E2E;
    }

    #header.active1 ul {
        opacity: 1;
        top: 75px;
    }

    #header ul li a {
        margin: 15px 0;
        color: #fff;
    }

    .toggle {
        display: block;
        position: relative;
        width: 40px;
        height: 40px;
        margin-right: 20px;
        background-image:
            url(https://img.icons8.com/material-outlined/48/ffffff/menu--v1.png);
        cursor: pointer;
    }

    #header.active1 .toggle {
        background-image:
            url(https://img.icons8.com/material-rounded/48/ffffff/multiply--v1.png);
        background-repeat: no-repeat;
    }
}





















/* Dropdown container - needed to position the dropdown content */
.dropdown {
    overflow: hidden;
    width: 100%;
    display: block;
    white-space: pre-wrap;
    box-sizing: border-box;
}

/* Style the dropdown button to fit inside the topnav */
.dropdown .dropbtn {
    font-size: 17px;
    border: none;
    outline: none;
    color: white;
    background-color: inherit;
    font-family: inherit;
    margin: 0;
}

/* Style the dropdown content (hidden by default) */
.dropdown-content {
    display: none;
    position: absolute;
    background-color: #898989;
    color: white;
    width: 350px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index: 1;
    text-align: center;
    align-items: center;
    margin-left: -150px;
    overflow: hidden;
    box-sizing: inherit;

}

/* Style the links inside the dropdown */
.dropdown-content a {
    float: none;
    color: black;
    text-decoration: none;
    display: block;
    text-align: left;
    margin: 5px;
}

/* Add a grey background to dropdown links on hover */
.dropdown-content a:hover {
    background-color: grey;
}

/* Show the dropdown menu when the user moves the mouse over the dropdown button */
.dropdown:hover .dropdown-content {
    display: block;
}

/* The "responsive" class is added to the topnav with JavaScript when the user clicks on the icon. This class makes the topnav look good on small screens (display the links vertically instead of horizontally) */
@media screen and (max-width: 749px) {
    .dropdown {
        display: flex;
        flex-direction: column;
    }

    #header.responsive {
        position: relative;
    }

    #header .dropdown-content {
        position: relative;
        margin-left: 0;
        width: 100%;
        padding: 10px;
    }
}

#header ul li .dropdown a {
    font-size: 24px;
}

@media screen and (min-width: 401px) {
    #header ul li .dropdown a {
        font-size: 24px;
    }
}

@media screen and (max-width: 400px) {
    #header ul li .dropdown a {
        font-size: 16px !important;
    }
}